<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process166"></a>

<p><br></p>
<p><span class="rvts17">浮木城竞技场（</span><span class="rvts50">The Driftwood Arena</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:406,Y:819</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：穆尔佳（</span><span class="rvts20">Murga</span><span class="rvts21">）</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">第一轮在视野受限的情况下击败竞技对手</span>
<br><span class="rvts21">第二轮击败虚空虫子和穆尔佳</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1144.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts21">浮木城的竞技场就在酒馆的下层。和竞技场的头领谈话，他会告诉你竞技场的上界冠军是穆尔佳（</span><span class="rvts20">Murga</span><span class="rvts21">），需要找到穆尔佳并向她发起挑战。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1145.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts21">穆尔佳的位置位于竞技场的虚空虫笼子旁边，和她谈话后，再折回头与竞技场头领谈话，便可以开始对局。</span>
<br><span class="rvts21">对局分为两场，第一场对局需要蒙眼对付一群挑战者，由于蒙眼造成射击范围和技能施展范围大大缩短，因此最好的对付策略就是躲在视野死角中（积攒行动点），让敌人来死角找你。</span>
<br><span class="rvts21">第二场对局需要对抗穆尔佳，穆尔佳的首发必使队伍的一名角色受到魅惑，但是可以通过寒冰盔甲进行驱除。在进入到第二轮战斗时，虚空虫乱入。然而虚空虫看似彪悍，其实很容易打败。</span>
<br><span class="rvts21">在杀掉虚空虫后，再次对抗穆尔佳，击败穆尔佳，便会取得竞技场的胜利。（从穆尔佳的尸体上可以搜出一把进入竞技场宝库的钥匙）</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1146.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts21">使用穆尔佳身上的钥匙，可以进入竞技场的藏宝点。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1147.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts21">藏宝室里的谜题比较容易解决，先传送一名队友把喷油的管子阀门关闭掉，之后便可以捡捡捡了。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



